@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    // цвет используется только здесь
    background-color: hsla(0deg, 0%, 0%, 1);

    width: 100%;
    height: 100vh;
}

.header {
    display: flex;
    position: fixed;
    z-index: var(--ym-z-index-header);
    align-items: center;
    gap: var(--ym-spacer-size-l);

    inset-block-start: var(--ym-spacer-size-xxxl);

    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    width: 100%;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        justify-content: space-between;
        gap: 0;

        inset-block-start: var(--ym-spacer-size-m);

        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

.logoLink {
    &_isOld {
        width: 169px;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        order: 2;

        width: 64%;
    }
}

.slot {
    flex: 1;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: none;
        order: 4;
    }
}

.slidesResult {
    display: flex;
    gap: var(--ym-spacer-size-xxxs);

    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);

    color: var(--ym-controls-color-secondary-text-enabled);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        order: 3;
    }
}

.activeItem {
    color: var(--ym-controls-color-secondary-on_default-enabled);
}

.backButton {
    color: var(--ym-controls-color-secondary-text-enabled);

    &:hover {
        color: var(--ym-controls-color-secondary-text-enabled_variant);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        order: 1;
    }
}

.desktopBackButton {
    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: none;
    }
}

.mobileBackButton {
    display: none;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: block;
    }
}

.empty {
    padding-block-start: calc(var(--ym-spacer-size-xxxxl) * 2);
    padding-block-end: calc(var(--ym-spacer-size-xxxxl) * 2);
    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);
}
