@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    position: absolute;
    inset-block-start: 50%;
    inset-inline-start: 100%;

    flex-direction: column;
    align-items: center;
    transform: translate(50%, -50%);
    gap: var(--ym-spacer-size-l);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        display: none;
    }
}

.top {
    transform: rotate(180deg);
}
