@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

/* stylelint-disable a11y/media-prefers-reduced-motion */
.cover {
    flex-shrink: 0;

    transition: clip-path var(--ym-duration-transition);

    margin-inline-end: var(--ym-spacer-size-m);

    clip-path: none;

    width: 100%;
    max-width: 80px;
    height: 100%;
    max-height: 80px;

    @include aspectRatio(1, 1);

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.root {
    display: flex;
    align-items: center;

    transform: translateY(150%);

    animation: animation 1s forwards;

    opacity: 0;

    outline: none;

    width: 100%;

    &_variant {
        &_quiz {
            border-radius: var(--ym-radius-size-l);
            background-color: var(--ym-controls-color-secondary-default-enabled);

            cursor: pointer;

            padding: var(--ym-spacer-size-m);

            &:hover,
            &:focus-visible {
                background-color: var(--ym-controls-color-secondary-default-hovered);
            }
        }

        &_artists {
            &:not(:last-child) {
                .cover {
                    animation: clip 0.4s forwards;
                    animation-delay: var(--slide-clip-delay);

                    @media screen and (prefers-reduced-motion: reduce) {
                        animation: none;
                    }
                }
            }
        }
    }

    @media screen and (prefers-reduced-motion: reduce) {
        animation: animation 0s forwards;
    }
}

.order {
    margin-inline-end: var(--ym-spacer-size-xs);

    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.image {
    width: 100%;
    height: 100%;
}

.meta {
    display: flex;
    flex: 1;
    flex-direction: column;

    max-width: 100%;

    overflow: hidden;
}

.title {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.subtitle {
    color: var(--ym-controls-color-secondary-text-enabled);
}

.icon {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

@keyframes clip {
    0% {
        clip-path: path(
            'M40 0C17.9086 0 0 17.9086 0 40C0 58.3168 12.3116 73.7581 29.1111 78.5C32.5729 77.5228 36.2254 77 40 77C43.7746 77 47.4271 77.5228 50.8889 78.5C67.6884 73.7581 80 58.3168 80 40C80 17.9086 62.0914 0 40 0Z'
        );
    }

    50% {
        clip-path: path(
            'M40 0C17.9086 0 0 17.9086 0 40C0 56.2523 9.69268 70.2407 23.6128 76.5C28.614 74.2512 34.1609 73 40 73C45.8391 73 51.386 74.2512 56.3872 76.5C70.3073 70.2407 80 56.2523 80 40C80 17.9086 62.0914 0 40 0Z'
        );
    }

    100% {
        clip-path: path(
            'M40 0C17.9086 0 0 17.9086 0 40C0 54.3546 7.56133 66.9432 18.9183 74C25.0395 70.1965 32.2632 68 40 68C47.7368 68 54.9605 70.1965 61.0817 74C72.4387 66.9432 80 54.3546 80 40C80 17.9086 62.0914 0 40 0Z'
        );
    }
}

@keyframes animation {
    0% {
        transform: translateY(150%);

        opacity: 0;
    }

    100% {
        transform: translateY(var(--slide-final-translate));

        opacity: 1;
    }
}
