.root {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-m);

    border-radius: var(--ym-radius-size-xl);

    background-color: var(--ym-controls-color-secondary-default-enabled);

    padding: var(--ym-spacer-size-l);

    &:hover,
    &:focus-visible {
        background-color: var(--ym-controls-color-secondary-default-hovered);
    }
}

.cover {
    width: 100%;
    max-width: 52px;
    height: 100%;
    max-height: 52px;
}

.image {
    width: 100%;
    height: 100%;
}

.text {
    flex: 1;

    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.icon {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}
