@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
}

.description {
    margin-block-end: var(--ym-spacer-size-xxxxl);

    text-align: center;

    color: var(--ym-controls-color-secondary-text-enabled_variant);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        margin-block-end: var(--ym-spacer-size-l);
    }
}

.cover {
    width: 100%;
    max-width: 250px;
    height: 100%;
    max-height: 250px;

    @include aspectRatio(1, 1);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        max-width: 170px;
        max-height: 170px;
    }
}

.meta {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-block-start: var(--ym-spacer-size-xs);

    width: 100%;

    text-align: center;

    &_withCover {
        transform: translateY(calc(var(--ym-spacer-size-m) * -1));

        margin-block-start: 0;
    }
}

.smallRoundCover {
    margin-block-end: var(--ym-spacer-size-xxxs);

    width: 100%;
    max-width: 52px;
    height: 100%;
    max-height: 52px;

    @include aspectRatio(1, 1);
}

.image {
    width: 100%;
    height: 100%;
}

.subtitle {
    width: 100%;

    color: var(--ym-controls-color-secondary-text-enabled);
}

.title {
    color: var(--ym-controls-color-secondary-text-enabled_variant);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        line-height: var(--ym-font-line-height-headline-m);
    }
}
