@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

/* stylelint-disable a11y/media-prefers-reduced-motion */
.root {
    width: 100%;
    height: 100%;
}

.content {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    animation: fade 0.5s forwards;

    opacity: 0;

    padding: var(--ym-spacer-size-l);

    width: 100%;
    height: 100%;

    @media screen and (prefers-reduced-motion: reduce) {
        animation: fade 0s forwards;
    }
}

.button {
    position: absolute;

    animation: fade 0.5s forwards;

    opacity: 0;

    inset-inline-start: var(--ym-spacer-size-xxl);
    inset-inline-end: var(--ym-spacer-size-xxl);
    inset-block-end: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        inset-block-end: var(--ym-spacer-size-l);
    }

    @media screen and (prefers-reduced-motion: reduce) {
        animation: fade 0s forwards;
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
