@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    opacity: 0.5;

    margin: auto;

    background-color: var(--ym-background-color-primary-enabled-content);

    max-height: calc(100vh - 2 * var(--ym-spacer-size-l) - 2 * var(--ym-spacer-size-xxxl));

    &_isActive {
        opacity: 1;

        @include aspectRatio(9, 16);
    }

    @include aspectRatio(8, 16);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        aspect-ratio: auto;

        width: 100%;
        height: calc(100vh - 2 * var(--ym-spacer-size-l) - 2 * var(--ym-spacer-size-xxxl));
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
