/* stylelint-disable a11y/media-prefers-reduced-motion */
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    position: relative;
    transform: scale(0);

    transition: opacity var(--ym-duration-transition), transform 1s;

    opacity: 0;

    margin: auto;

    height: 100vh;

    &_visible {
        animation: fade 1s ease forwards;
        animation-delay: 0.5s;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: fade 0s ease forwards;
        }
    }

    @include aspectRatio(9, 16);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        aspect-ratio: auto;
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.container {
    width: 100%;
    height: 100%;

    overflow: hidden;
}

@keyframes fade {
    0% {
        transform: scale(0);

        opacity: 0;
    }

    100% {
        transform: scale(1);

        opacity: 1;
    }
}
