.root {
    position: relative;

    // TODO: заменить на фолбэчный цвет
    background-color: var(--ym-background-color-primary-enabled-content);

    width: 100%;
    height: 100%;
}

.fade,
.media {
    position: absolute;

    inset-block-start: 0;
    inset-inline-start: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
}
